<template>
    <div>
        <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>我的日报</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区 -->
        <el-card class="box-card">
          <!-- 卡片内容区 -->
          <div>
            <!-- 卡片内容区头部 -->
            <el-row>
                  <el-button type="primary" size="middle" icon="el-icon-circle-plus-outline" @click="addDialogVisibleHandle" plain  >新增日报</el-button>
            </el-row>
            <!-- 卡片内容表单区 -->
            <el-row>
              <el-table  :data="tableData" style="width: 100%" stripe border >
                <el-table-column  type="index" width="50"> </el-table-column>
                <el-table-column   prop="workReportUserName"    label="人员"   width="100"> </el-table-column>
                <el-table-column    prop="workReportType"   label="日报类型" width="100"  >
                  <template slot-scope="scope" >
                   <el-tag type="success" v-if="scope.row.workReportType===0">日报</el-tag>
                   <el-tag  v-else>周报</el-tag>
                  </template>

                </el-table-column>
                <el-table-column   prop="workReportContent"   label="内容" width="300" >
                  <template slot-scope="scope">
                   <div v-html="scope.row.workReportContent"></div>
                  </template>
                </el-table-column>
                <el-table-column  prop="workReportCreatedDate"  label="创建时间"    width="160" > </el-table-column>
                <el-table-column    prop="workReportUpdatedDate"   label="更新时间"  width="160" > </el-table-column>
                <el-table-column label="操作" >
                    <template slot-scope="scope">
                      <div v-if="userType!==2 && scope.row.reviews">
                        <!-- reviews -->
                        <el-button type="primary" size="mini" plain  @click="dailyDetailHandle(scope.row.workReportId)">已点评</el-button>
                        <el-button type="primary" size="mini" plain  @click="checkedHandle(scope.row.workReportId)"  >查看</el-button>
                      </div>
                      <div v-else-if="userType!==2 && !scope.row.reviews">
                        <el-button type="primary" size="mini" plain  @click="dailyDetailHandle(scope.row.workReportId)"  >点评</el-button>
                        <el-button type="primary" size="mini" plain  @click="checkedHandle(scope.row.workReportId)"  >查看</el-button>
                      </div>
                      <el-button type="primary" size="mini" plain  @click="checkedHandle(scope.row.workReportId)" v-else >查看</el-button>
                    </template>
                </el-table-column>
              </el-table>
            </el-row>
            <!-- 卡片内容分页区 -->
            <!-- <el-pagination  :page-sizes="[10, 20, 30, 40]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="totalNum">
            </el-pagination> -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalNum">
      </el-pagination>
          </div>
      </el-card>

      <!-- 点击新增按钮后 弹出的对话框 -->
      <el-dialog  title="工作日报"  :visible.sync="addDailyVisible" width="50%"  @close="workDailyDialogClose" :close-on-click-modal="false" >
        <el-form :model="addDailyRuleForm" :rules="addDailyRules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="日报类型" prop="region">
            <el-select v-model="addDailyRuleForm.region" placeholder="请选择日报类型">
              <el-option label="日报" value="0"></el-option>
              <el-option label="周报" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="新增日期" required>
            <el-col :span="10">
              <el-form-item prop="date1">
                <el-date-picker  type="date" placeholder="选择日期" v-model="addDailyRuleForm.date1" style="width: 100%;"></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="日报内容" prop="desc">
            <el-input type="textarea" v-model="addDailyRuleForm.desc" :autosize="{ minRows: 4 }" :placeholder="placeholderText" ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addDailySubmitForm('ruleForm')">提交</el-button>
            <el-button @click="addDailyResetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

       <!-- 点评 弹出的对话框 -->
      <el-dialog  title="点评"  :visible.sync="commentDailyVisible" width="50%"
      @close="commentDailyDialogClose" >
        <el-form :model="commentDailyRuleForm" :rules="commentDailyRules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="点评内容" prop="desc">
            <!-- <tempalte slot-scope="scope"> -->
              <el-input type="textarea" v-model="commentDailyRuleForm.desc" :autosize="{ minRows: 4 }" :placeholder="placeholderText" ></el-input>
              <!-- <el-input type="textarea" v-model="scope.row.reviews" :autosize="{ minRows: 4 }" :placeholder="placeholderText" ></el-input> -->
            <!-- </tempalte> -->
            <!-- <el-input type="textarea" v-model="commentDailyRuleForm.desc" :autosize="{ minRows: 4 }" :placeholder="placeholderText" ></el-input> -->
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="commentDailySubmitForm('ruleForm')">提交</el-button>
            <el-button @click="commentDailyResetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
   </div>
</template>
<script>
import Qs from 'qs'
export default {

  data () {
    return {
      tableData: [],
      // 搜索框value值
      searchInput: '',
      commentDailyVisible: false,
      addDailyVisible: false,
      // 新增日报表单提交值
      addDailyRuleForm: {
        region: '',
        date1: new Date(),
        desc: '',
        // 最终格式化后的时间格式
        finallyDate: '',
        // 最终格式化后的文本格式
        finallyContent: ''

      },
      // 新增日报表单的预校验规则
      addDailyRules: {
        region: [
          { required: true, message: '请选择活动区域', trigger: 'change' }
        ],
        date1: [
          { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '请填写日报内容', trigger: 'blur' }
        ]
      },
      // 评论新增日报表单提交值
      commentDailyRuleForm: {
        desc: '',
        // 最终格式化后的文本格式
        finallyContent: ''
      },
      // 评论新增日报表单的预校验规则
      commentDailyRules: {
        desc: [
          { required: true, message: '请填写评论内容', trigger: 'blur' }
        ]
      },
      // 表单文本域默认值
      placeholderText: '1.xxxxxxx\n2.xxxxxxx\n3.xxxxxxx',
      userName: '',
      userId: '',
      // 总数据条数
      totalNum: null,
      // 页面数据条数
      pageSize: 10,
      // 当前页数
      currentPage: 1,
      userType: null,
      workReportId: ''
    }
  },
  created () {
    this.userId = window.sessionStorage.getItem('userId')
    this.userType = window.sessionStorage.getItem('userType') - 0
    this.getUserInfo()
    this.getTableData()
  },
  methods: {
    handleCurrentChange (newPage) {
      this.currentPage = newPage
      this.getTableData()
    },
    handleSizeChange (newSize) {
      this.pageSize = newSize
      this.getTableData()
    },
    // 获取用户信息
    async  getUserInfo () {
      const data = Qs.stringify({ userId: this.userId })
      const { data: res } = await this.$http.post('/user/findUser', data)
      this.userName = res.data.userName
    },
    // 获取日报列表
    async getTableData () {
      const data = Qs.stringify({ workReportUserId: this.userId, pageSize: this.pageSize, pageNum: this.currentPage })
      const { data: res } = await this.$http.post('/workReport/findWorkReportByUserId', data)
      if (res.status !== 200) return this.$message.error('获取日报列表失败！')
      this.tableData = res.data.items
      this.totalNum = res.data.totalNum
      this.pageSize = res.data.pageSize
      this.currentPage = res.data.currentPage
    },
    // 点击新增按钮后触发该事件
    addDialogVisibleHandle () {
      this.addDailyVisible = true
    },
    // 点击表格操作栏详情按钮触发该事件
    dailyDetailHandle (id) {
      this.workReportId = id
      this.commentDailyVisible = true
    },
    // 点击新增按钮弹出新增日报对话框，点击提交按钮时触发该事件
    addDailySubmitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 调用该事件将，将表单内格林时间格式的对象转化格式为 2019/11/22
          this.addDailyRuleForm.finallyDate = this.timeChange(this.addDailyRuleForm.date1)
          // 在表单提交信息内新增content，为最终参数
          this.addDailyRuleForm.finallyContent = this.preText(this.addDailyRuleForm.desc)
          // console.log(this.addDailyRuleForm)
          const data = {
            workReportContent: this.addDailyRuleForm.finallyContent,
            workReportType: this.addDailyRuleForm.region,
            workReportUserId: this.userId,
            workReportUserName: this.userName
          }
          //  新增工作日报
          const { data: res } = await this.$http.post('/workReport/addWorkReport', Qs.stringify(data))
          if (res.status !== 200) {
            this.addDailyVisible = false
            this.addDailyResetForm('ruleForm')
            this.getTableData()
            return this.$message.error(res.msg)
          } else {
            this.addDailyVisible = false
            this.addDailyResetForm('ruleForm')
            this.getTableData()
            return this.$message.success(res.msg)
          }
        } else {
          return false
        }
      })
    },
    // 点击新增按钮弹出新增日报对话框，点击重置按钮时触发该事件
    addDailyResetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 新增日报弹出对话框点击关闭后触发该事件
    workDailyDialogClose () {
      // console.log(1)
      // 关闭后重置表单内容
      this.addDailyResetForm('ruleForm')
    },
    // 评论点击提交按钮时触发该事件
    commentDailySubmitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 调用该事件将，将表单内格林时间格式的对象转化格式为 2019/11/22
          this.commentDailyRuleForm.finallyContent = this.preText(this.commentDailyRuleForm.desc)
          const data = Qs.stringify({ workReportId: this.workReportId, reviewsUserId: window.sessionStorage.getItem('userId'), reviewsUserName: window.sessionStorage.getItem('userName'), reviews: this.commentDailyRuleForm.finallyContent })
          const { data: res } = await this.$http.post('/workReport/addReviews', data)
          if (res.status !== 200) return this.$message.error(res.msg)
          if (res.status === 200) {
            this.commentDailyResetForm('ruleForm')
            this.commentDailyVisible = false
            return this.$message.success(res.msg)
          }
        } else {
          return false
        }
      })
    },
    // 点击新增按钮弹出新增日报对话框，点击重置按钮时触发该事件
    commentDailyResetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 新增日报弹出对话框点击关闭后触发该事件
    commentDailyDialogClose () {
      // console.log(1)
      // 关闭后重置表单内容
      this.commentDailyResetForm('ruleForm')
    },
    // 点击查看按钮触发该事件
    checkedHandle (workReportId) {
      this.$router.push({ path: '/work/checkedDaily', query: { id: workReportId } })
    },
    //  自己分装的函数区域
    //  将格林时间格式的对象转化格式为 2019/11/22 11:25:50
    timeChange (time) {
      var time1 = time.getTime()
      var date = new Date(time1)
      var Y = date.getFullYear() + '/'
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '/'
      var D = date.getDate()
      var current = Y + M + D
      return current
    },
    //  将文本域的文本进行处理让文本保存换行空格形式，传递给后端
    preText (pretext) {
      return pretext.replace(/\r\n/g, '<br/>').replace(/\n/g, '<br/>').replace(/\s/g, '&nbsp;')
    }
  }
}
</script>
<style lang="less" scoped>
.icon-s-operation{
    font-size: 20px;
    color: black;
}
.my_select{
    height: 32px!important;
}
.icon-s-fold{
    font-size: 18px;
}
.icon-s-fold-button{
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    &:hover{
        background-color: #000;
        .icon-s-fold{
            color: #ffffff;
        }
    }
}
.daily_input{
  margin: 0 20px;
  width: 300px;
}
.line-center{
  text-align: center;
}

</style>
