<template>
    <div>
         <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/work/daily' }">我的日报</el-breadcrumb-item>
          <el-breadcrumb-item>日报详情</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区 -->
        <el-card>
            <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>日报详情</span>
               <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button>
            </div>
            <div class="cardContent">
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">日报类型</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight textRightColor">
                        <el-tag v-if="dailyDeatil.workReportType===0" type="success" >日报</el-tag>
                        <el-tag v-else>周报</el-tag>
                      </span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">创建时间</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{dailyDeatil.workReportCreatedDate}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">更新时间</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{dailyDeatil.workReportUpdatedDate}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">日报内容</span>
                  </el-col>
                   <el-col :span="12">
                     <div class="textRight lineHeight " v-html="dailyDeatil.workReportContent">
                        <!-- <div class="textRight " ></div> -->
                      </div>
                  </el-col>
                </el-row>
                 <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">评论人</span>
                  </el-col>
                   <el-col :span="12">
                      <div class="textRight">
                        <span style="color:#7c7cfa;">{{dailyDeatil.reviewsUserName}}</span>
                      </div>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">评论内容</span>
                  </el-col>
                   <el-col :span="12">
                      <div class="textRight lineHeight" v-html="dailyDeatil.reviews">
                        <!-- <span>{{dailyDeatil.reviewsUserName}}：</span> -->
                        <!-- <div class=" " ></div> -->
                      </div>
                  </el-col>
                </el-row>
            </div>
        </el-card>
    </div>
</template>
<script>
import Qs from 'qs'

export default {
  data () {
    return {
      dailyDeatil: {}
    }
  },
  created () {
    // this.dailyDeatil = this.$route.query
    this.getDailyDeatil()
  },
  methods: {
    // 获取当前日报详情
    async getDailyDeatil () {
      const { data: res } = await this.$http.post('/workReport/findWorkReportById', Qs.stringify({ workReportId: this.$route.query.id }))
      this.dailyDeatil = res.data
    },
    // 点击返回按钮 触发该事件
    returnUpadataHandle () {
      this.$router.push('/update/customerUpdate')
    },
    // 点击返回触发该事件，返回上一页
    returnHandle () {
      this.$router.push('/work/daily')
    }
  }
}
</script>

